.visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  white-space: nowrap;
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

:focus-visible {
  outline-offset: 2px;
  outline: 2px solid #20c8ff;
}

.fade-img {
  opacity: 0;
  pointer-events: none;
  transition: all 1.3s ease-out;
}

.fade-img--up {
  transform: translateY(100%);
}

.fade-img--down {
  transform: translateY(-100%);
}

.fade-img--left {
  transform: translateX(-100%);
}

.fade-img--right {
  transform: translateX(100%);
}

.fade-img--visible {
  opacity: 1;
  pointer-events: all;
  transform: translateY(0)translateX(0);
}

.menu-btn {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  color: #f1f5f4;
  background-color: #3f5252;
  border: 0;
  transition: all .3s;
}

@media (hover: none) {
  .menu-btn {
    cursor: default;
  }
}

.menu-btn:disabled {
  cursor: not-allowed;
}

@media (hover: hover) {
  .menu-btn:hover {
    transform: translateY(-2px);
    box-shadow: 2px 5px 5px #1c1b29cc, 5px 5px 5px #1c1b2914;
  }

  .menu-btn:hover .menu-btn__icon {
    opacity: .8;
  }
}

.menu-btn__icon {
  background-color: currentColor;
  width: 30px;
  height: 30px;
  transition: all .3s;
  display: block;
  mask-image: url("menu.f53d6c2d.svg");
  mask-position: center;
  mask-repeat: no-repeat;
  mask-clip: content-box;
}

.menu-btn--close .menu-btn__icon {
  mask-image: url("cross.06a2fa18.svg");
}

.menu-btn:active {
  transform: translateY(1px);
  box-shadow: 1px 2px 4px #1c1b29cc, 2px 2px 4px #1c1b2914;
}

.menu-btn:focus-visible {
  outline: none;
}

.menu-btn:focus-visible .menu-btn__icon {
  background-color: #20c8ff;
  transform: scale(1.1);
}

.button {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  color: #f1f5f4;
  border: 1px solid #cd4d31;
  justify-content: center;
  align-items: center;
  gap: 8px;
  height: 60px;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

@media (hover: none) {
  .button {
    cursor: default;
  }
}

.button:disabled {
  cursor: not-allowed;
}

.button--primary {
  background-color: #cd4d31;
}

@media (hover: hover) {
  .button--primary:hover {
    background-color: #af3419;
    border-color: #af3419;
  }
}

.button--primary:active {
  background-color: #ea340d;
  border-color: #ea340d;
}

.button--primary:disabled {
  background-color: #dd836f;
  border-color: #dd836f;
}

.button--secondary {
  color: #cd4d31;
  background-color: #f1f5f4;
}

@media (hover: hover) {
  .button--secondary:hover {
    color: #af3419;
    border-color: #af3419;
  }
}

.button--secondary:active {
  color: #ea340d;
  border-color: #ea340d;
}

.button--secondary:after {
  content: "";
  background-color: currentColor;
  width: 20px;
  height: 20px;
  display: block;
  mask-image: url("arrow.fc541252.svg");
  mask-position: center;
  mask-repeat: no-repeat;
  mask-clip: content-box;
}

.button--secondary:disabled {
  color: #dd836f;
  border-color: #dd836f;
}

.language-selector {
  z-index: 20;
}

.language-selector__button {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  text-transform: uppercase;
  color: #f1f5f4;
  background-color: #3f5252;
  border: 1px solid #3f5252;
  justify-content: center;
  align-items: center;
  gap: 5px;
  height: 100%;
  font-weight: 500;
  line-height: 150%;
  transition: color .3s, transform .3s;
  display: flex;
  position: relative;
}

@media (hover: none) {
  .language-selector__button {
    cursor: default;
  }
}

.language-selector__button:disabled {
  cursor: not-allowed;
}

@media (hover: hover) {
  .language-selector__button:hover:after {
    color: #af3419;
  }
}

.language-selector__button:after {
  content: "";
  background-color: currentColor;
  width: 8px;
  height: 5px;
  transition: transform .3s, color .3s;
  display: block;
  transform: rotate(0);
  mask-image: url("dropdown.a04990c7.svg");
  mask-position: center;
  mask-repeat: no-repeat;
  mask-clip: content-box;
}

.language-selector__button:active:after {
  color: #ea340d;
}

.language-selector__button--hidden {
  display: none;
}

.language-selector__button--dropdown-visible:after {
  color: #cd4d31;
  transform: rotate(180deg);
}

.language-selector__dropdown {
  opacity: 0;
  visibility: hidden;
  background-color: #f1f5f4;
  width: 100%;
  transition: opacity .3s, transform .3s;
  position: absolute;
  transform: translateY(-50%);
  box-shadow: 2px 5px 5px #1c1b29cc, 5px 5px 5px #1c1b2914;
}

@media (width >= 768px) {
  .language-selector__dropdown {
    right: 0;
  }
}

.language-selector__dropdown--visible {
  opacity: 1;
  visibility: visible;
  transform: translateY(10px);
}

.language-selector__list {
  list-style: none;
}

.language-selector__item {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  color: #1c1b29;
  border: 0;
  width: 100%;
  padding: 10px 5px;
  display: block;
}

@media (hover: none) {
  .language-selector__item {
    cursor: default;
  }
}

.language-selector__item:disabled {
  cursor: not-allowed;
}

@media (hover: hover) {
  .language-selector__item:hover {
    color: #af3419;
  }
}

@media (width >= 768px) {
  .language-selector__item {
    text-align: right;
  }
}

.language-selector__item:active {
  color: #ea340d;
}

.language-selector__item[aria-selected="true"] {
  pointer-events: none;
  color: #f1f5f4;
  background-color: #cd4d31;
  border-color: #cd4d31;
}

